import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AngularFireAuth } from 'angularfire2/auth';

@Component({ 
  selector: 'app-programme',
  templateUrl: './programme.page.html',
  styleUrls: ['./programme.page.scss'],
})
export class ProgrammePage implements OnInit {
  motsparjour =20;
  diapo1 : boolean;
  diapo2 : boolean;
  diapo3 : boolean;
  a=0;
  datejour= new Date();
  date1 = this.datejour.toDateString();
  dateobject = [{date : this.date1}]


  cUser = this.fAuth.auth.currentUser.uid;

  fiches;
  constructor(private httpCLient: HttpClient,public fAuth: AngularFireAuth) { }
  
  btndiapo1() {
    this.diapo1 = false;
    this.diapo2 = true;
  };
  btndiapo2() {
    this.diapo2 = false;
    this.diapo3 = true;
    this.httpCLient.put( 'https://flash-2ad53.firebaseio.com/'+ this.cUser +'/motsparjour.json' , this.motsparjour)
    .subscribe(()=> {
      console.log('enregistrement terminé');
    },
      (error)=> {
        console.log('erreur de sauvegarde');
      }
      )

      this.httpCLient.put( 'https://flash-2ad53.firebaseio.com/'+ this.cUser +'/compteur.json' , this.a )
      .subscribe(()=> {
        console.log('enregistrement terminé');
      },
        (error)=> {
          console.log('erreur de sauvegarde');
        }
        )
      
      this.httpCLient.put( 'https://flash-2ad53.firebaseio.com/'+ this.cUser +'/date1.json' , this.dateobject)
      .subscribe(()=> {
          console.log('enregistrement terminé');
      },
        (error)=> {
          console.log('erreur de sauvegarde');
        }
        )


    }
  CopyFichesToServeur() {
    this.httpCLient.get<any>( 'https://flash-2ad53.firebaseio.com/Fiches.json' )
    .subscribe ( (response) => {
      this.fiches = response;
      console.log ('copie effectuée');
      this.httpCLient.put( 'https://flash-2ad53.firebaseio.com/'+ this.cUser +'/fiches.json' , this.fiches)
    .subscribe(()=> {
      console.log('enregistrement terminé');
    },
      (error)=> {
        console.log('erreur de sauvegarde');
      }
      )

    },
        (error) => {
          console.log ('erreur de chargement' + error);
        }
    );
  }



  ngOnInit() {
    this.diapo1=true;
    this.CopyFichesToServeur();
    console.log()
  }

}
